/*
* 专项费用表
* * dangwbocspace.cn
* */
import React from 'react';
import {Breadcrumb, Col, Row, Table, Card, Affix, Menu, Dropdown, Button, Icon, ConfigProvider} from 'antd';
import ReactEcharts from 'echarts-for-react';
import {Scrollbars} from 'react-custom-scrollbars';
import url from 'url';
import moment from 'moment';
import {inject} from "mobx-react";
import * as mobx from 'mobx';

const dataObj = {
	'01': '1-1月',
	'02': '1-2月',
	'03': '1-3月',
	'04': '1-4月',
	'05': '1-5月',
	'06': '1-6月',
	'07': '1-7月',
	'08': '1-8月',
	'09': '1-9月',
	'10': '1-10月',
	'11': '1-11月',
	'12': '1-12月',
	'season1': '1-3月',
	'season2':'4-6月',
	'season3':'7-9月',
	'season4':'10-12月',
	'fullyear':'1-12月'
}

const height = {
	0: '414px',
	1:'550px',
	2: '380px'
}

const customizeRenderEmpty = () => (
	<div style={{ textAlign: 'center',padding: '50px 0'}}>
		<img
			width='150px'
			height="100px"
			src='./images/not_found.png' />
		<p style={{
			fontSize:'14px',
			fontFamily:'PingFangSC-Regular,PingFang S',
			fontWeight: '400',
			color: 'rgba(75,80,81,1)'
		}}>暂无数据</p>
	</div>
);

@inject('kanbanStore')
export default class Zxfy extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			yearData: [{
				id: '',
				name: '2019'
			}],  // 年份
			selData: [
				{
					id: '01',
					name: '1月'
				}, {
					id: '02',
					name: '2月'
				}, {
					id: '03',
					name: '3月'
				}, {
					id: '04',
					name: '4月'
				}, {
					id: '05',
					name: '5月'
				}, {
					id: '06',
					name: '6月'
				}, {
					id: '07',
					name: '7月'
				}, {
					id: '08',
					name: '8月'
				}, {
					id: '09',
					name: '9月'
				}, {
					id: '10',
					name: '10月'
				}, {
					id: '11',
					name: '11月'
				}, {
					id: '12',
					name: '12月'
				}, {
					id: 'season1',
					name: '1季度'
				}, {
					id: 'season2',
					name: '2季度'
				}, {
					id: 'season3',
					name: '3季度'
				}, {
					id: 'season4',
					name: '4季度'
				}, {
					id: 'fullyear',
					name: '年度'
				}
			],
			value: '年度',
			valKey: '',
			value1: '2019',
			key: '1',
			id: '', // 合同表id
			time: null, // 最后更新时间
			capitalData: [], // 数据
			tableData: [],  // 3个图表数据
			columns: [
				{
					title: '专项活动',
					dataIndex: 'tel',
					align: 'center',
				},
				{
					title: '累计',
					dataIndex: 'phone',
					align: 'center',
				}
			],
			month:''
		};
	}

	componentDidMount() {
        // 获取当前年份数据
		const currentYear = moment().year();
		/*
		* 年份数据来源：从2019年开始， 截至到当前的年份
		* */
		if (currentYear<=2019) {
			this.setState({
				yearData: [{
					name: '2019'
				}]
			});
		} else {
			const num = Number(currentYear);
			let data = [];
			for (let i=2019;i<=num;i++) {
				data.push({name:String(i)})
			}
			this.setState({
				yearData: Object.assign([], data.reverse())
			});
		}

		const data = {
			'01': '1月',
			'02': '2月',
			'03': '3月',
			'04': '4月',
			'05': '5月',
			'06': '6月',
			'07': '7月',
			'08': '8月',
			'09': '9月',
			'10': '10月',
			'11': '11月',
			'12': '12月',
			'season1': '1季度',
			'season2': '2季度',
			'season3': '3季度',
			'season4': '4季度',
			'fullyear': '年度'
		}
		const mont = moment().month() + 1;
		let search = this.props.location.search;
		const id = url.parse(search, true).query.id;
		const key = url.parse(search, true).query.key;
		const time = url.parse(search, true).query.time;
		const mon = url.parse(search, true).query.month;
		let month = mon == undefined ? mont : mon;
		let lastTime = null; // 最后更新时间

		// 没有时间
		if(time==undefined) {
			// 根据id查询时间
			this.getLastUpdatedTime().then((res) => {
				if (res) {
					let capitalInfo = res.filter(item => {
						return item.code === "zxfy"
					});
					if (capitalInfo.length > 0) {
						lastTime = capitalInfo[0]['lastUpdateTime'];
						this.setState({
							time: Number(lastTime)
						})
					}
				}
			});
		} else {
			this.setState({
				time: Number(time)
			})
		}

		this.setState({
			id,
			key,
			value: data[month],
			month: month.toString(),
			value1: currentYear.toString()
		}, () => {
			// 获取初始数据
			this.handleChange();
		});
	}

	// 获取最后时间
	getLastUpdatedTime = () => {
		const params = {
			"level": 2,
			"parentId": ""
		}
		return this.props.kanbanStore.getLastTime(params);
	}

	getReportByTime = (params) => {
		this.props.kanbanStore.getReportByClassifyId(params).then((res) => {
			if (res) {
				this.setState({
					capitalData: Object.assign([], res.filter((item) => {
						return item.name === "专项费用整体情况";
					})[0]['datas']),
					tableData:  res.filter((item) => {
						return item.name !== "专项费用整体情况";
					}).map((item, index) => {
						// charts : {},  datas : [], id : "",name : '', schemas : []
						return Object.assign(item, {
							schemas: item.schemas.map((vitem, vindex)=> {
								if (vindex == 0) {  // 第一列固定
									return {
										title: vitem.title,
										dataIndex: vitem.name,
										width: '150px',
										align: 'center',
										className: 'vt',
										fixed: 'left'
									}
								} else {
									return {
										title: vitem.title,
										dataIndex: vitem.name,
										align: 'center',
										className: 'vt',
									}
								}
							})
						})
					})
				})
			}
		});
	}

	handleMenuClick = (e) => {
		let value = e.key;
		this.setState({
			value: e.item.props.children,
			month: value
		}, ()=>{
			this.handleChange();
		});
	}

	// 切换年份
	handleYearClick = (e) => {
		this.setState({
			value1: e.item.props.children
		},()=>{
			this.handleChange();
		})
	}

	handleChange = () => {
		const {month, value1, id} = this.state;
		let params = {"classifyId": id, "year": value1, "beginMonth": "01", "endMonth": month};
		if (month === "season1") {
			params = {"classifyId": id, "year": value1, "beginMonth": "01", "endMonth": "03"};
		} else if (month === "season2") {
			params = {"classifyId": id, "year": value1, "beginMonth": "04", "endMonth": "06"};
		} else if (month === "season3") {
			params = {"classifyId": id, "year": value1, "beginMonth": "07", "endMonth": "09"};
		} else if (month === "season4") {
			params = {"classifyId": id, "year": value1, "beginMonth": "10", "endMonth": "12"};
		} else if (month === "fullyear") {
			params = {"classifyId": id, "year": value1, "beginMonth": "01", "endMonth": "12"};
		}
		this.getReportByTime(params);
	}

	// 动态图表
	changeOption = (params)  => {
		return {
			title: {
				text: params.chartName+"图",
				left:'center',
				textStyle:{
					color: 'rgb(61, 77, 102)',
					fontWeight:'bold',
					fontStyle:'normal',
					fontFamily: 'Microsoft YaHei UI',
					fontSize:'13'
				}
			},
			tooltip: {
				trigger: 'item',
				formatter: "{b} : {c} <br/>{a} : {d}%"
			},
			legend: {
				bottom: 0,
				data: params.category,
			},
			grid: {
				left: '2%',
				right: '4%',
				top:'3%',
				containLabel: true
			},
			toolbox: {
				show: true,
				feature: {
					mark: {show: true},
					dataView: {show: false, readOnly: false},
					magicType: {
						show: true,
						type: ['pie', 'funnel'],
						option: {
							funnel: {
								x: '25%',
								width: '45%',
								funnelAlign: 'left',
								max: 1548
							}
						}
					},
					restore: {show: false},
					saveAsImage: {show: false}
				}
			},
			calculable: true,
			series: [
				{
					name: '占比',
					type: 'pie',
					radius: ['40%', '65%'],
					center: ['50%', '50%'],
					label: {
						normal: {
							show: false,
							formatter: "{d}%",
							position: 'inner',
							textStyle: {
								color: '#FFF',
								fontSize: 12
							}
						}
					},
					data: params.series,
					color:['#87BDFA','#9C96F1','#63C8C6','#BADBF6','#AEDFF1','#F2D6BC','#8EABD0']
				}
			]
		}
	}


	render() {
		const {yearData, selData, value, value1, time, capitalData, tableData,key, month} = this.state;
		const columns1 = [
			{
				title: '全年专项费用',
				dataIndex: 'yearAll',
				align: 'center',
				className: 'vt',
			},
			{
				title: dataObj[month]+'累计',
				dataIndex: 'all',
				align: 'center',
				className: 'vt',
			},
			{
				title: '完成率',
				dataIndex: 'rate',
				align: 'center',
				className: 'vt',
			}
		];
		const menu = (
			<Scrollbars style={{height: 200}}>
				<Menu className="con_menu" onClick={this.handleMenuClick}>
					{selData.map((item, index) => {
						return (
							<Menu.Item key={item.id}>
								{item.name}
							</Menu.Item>
						)
					})}
				</Menu>
			</Scrollbars>
		);
		const menu1 = (
			<Scrollbars style={{height: 200}}>
				<Menu className="con_menu" onClick={this.handleYearClick}>
					{yearData.map((item, index) => {
						return (
							<Menu.Item key={'year' + index}>
								{item.name}
							</Menu.Item>
						)
					})}
				</Menu>
			</Scrollbars>
		);
		const {defaultKey} = this.props.kanbanStore;

		return (
			<div>
				<ConfigProvider renderEmpty={customizeRenderEmpty}>
				<div className="con_bottom">
					<Breadcrumb separator=">" style={{margin: '0'}}>
						<Breadcrumb.Item href={"#/home?key=" + defaultKey}>经营看板</Breadcrumb.Item>
						<Breadcrumb.Item href={"#/home?key=" + key}>财务</Breadcrumb.Item>
						<Breadcrumb.Item>专项费用表</Breadcrumb.Item>
					</Breadcrumb>
				</div>
				<Row>
					<Col span={24}>
						<div className="mt20 mb20">
							<span className="con_title">专项费用表 </span>
							<span className="con_time ml35">最近更新时间:</span><span
							className="con_time_content ml15">{moment(time).format("YYYY-MM-DD HH:mm:ss")}</span>
						</div>
					</Col>
					<Col span={24}>
						<Dropdown overlay={menu1}>
							<Button style={{width: 82}}>
								{value1} <Icon type="down"/>
							</Button>
						</Dropdown>
						<Dropdown overlay={menu}>
							<Button className="ml15" style={{width: 82}}>
								{value} <Icon type="down"/>
							</Button>
						</Dropdown>
					</Col>
				</Row>
				<Card bordered className="mt20" style={{width: '100%'}}>
					<h3 className="kanban_title mb20">专项费用整体情况（单位：万元）</h3>
					<Table columns={columns1}
					       dataSource={capitalData}
					       size={"small"}
					       pagination={false}
					       className="ci_table2"
					       title={() => '专项费用整体情况'}
					       bordered/>
				</Card>
				{tableData&&tableData.map((item, index)=>{
						return (<Card key={'card_'+index} bordered className="mt20" style={{width: '100%'}}>
								<h3 className="kanban_title mb20">{item.name+"情况"}（单位：万元）</h3>
								<Row>
									<Col span={10}>
										<ReactEcharts
											notMerge={true}
											lazyUpdate={true}
											option={this.changeOption(item.charts)}
											style={{height:height[index]}}/>
									</Col>
									<Col span={14}>
								<Table columns={item.schemas}
							       dataSource={item.datas}
							       size={"small"}
							       pagination={false}
							       className="ci_table2 ci_table5"
								   title={() => item.name+"明细"}
							       rowClassName={(record, index) => {
								       let className = index % 2 ? 'shallow_gray' : 'deep_gray_1';
								       return className
							       }}
							       scroll={{ x: item.schemas.length*100 }}
							       bordered/>
									</Col>
								</Row>
							</Card>
						)
					})}
				</ConfigProvider>
			</div>
		);
	}
}





